:root {
    --background-color: #fff;
    --background-light: #fff;
    --shadow-color: rgba(0, 0, 0, 0.2);
    --primary-color: rgb(33,
        150,
        243);
    --text-color: #0A0A0A;
    --text-light: #575757;
    --font-size: 16px;
    --animation-speed: 1;
}

@function getAnimationDuration($duration: number) {
    @return calc($duration * var(--animation-speed));
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-size: var(--font-size);
    font-family: 'Roboto', sans-serif;
    padding-top: 55px;
    background-color: var(--background-light);
}

a {
    text-decoration: none;
}

img {
    object-fit: cover;
}

.btn {
    border: none;
    cursor: pointer;
    outline: none;
    background-color: var(--primary-color);
    color: #fff;
    padding: .75em 1em;
    text-transform: uppercase;
    font-size: 1em;
    font-weight: bold;
    letter-spacing: 1px;
    border-radius: 3px;
    box-shadow: 0 2px 6px var(--shadow-color);

    &:hover {
        box-shadow: 0 1px 3px var(--shadow-color);
    }
}

.custom-image {
    height: 0;
    width: 100%;
    position: relative;

    img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}

@import "partials/navbar.scss";

.section {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 3vw;

    &.d-block {
        display: block;
    }

    .title {
        color: var(--text-color);
        font-size: 2.5em;
        margin-bottom: .25em;
        font-weight: 900;
    }

    .info {
        color: var(--text-light);
        font-size: 1em;
        letter-spacing: 1px;
        line-height: 1.3;
        margin-bottom: 1.25em;

    }

    .col {
        width: 50%;
    }
}

@import "partials/_hero-section.scss";
@import "partials/chiefs-sections.scss";
@import "partials/_improve-section.scss";
@import "partials/_quote-section.scss";
@import "partials/_footer.scss";
@import "partials/recipes-section.scss";
@import "partials/_previous-searces.scss";


.options-container {
    box-shadow: 0 5px 10px var(--shadow-color);
    padding: 1em;
    border-radius: 5px;
    border-left: .5em solid var(--primary-color);
    background-color: var(--background-light);
    margin-top: 1em;
    display: flex;
    gap: 1em;

    .option {
        height: 3.5em;
        width: 3.5em;
        box-shadow: 0 3px 6px var(--shadow-color);
        border-radius: 5px;
        display: flex;
        justify-content: center;
        align-items: center;

        &.light {
            background-color: #fff;
        }

        &.dark {
            background-color: rgb(29, 29, 29);
        }

        .check {
            height: 2.5em;
            width: 2.5em;
            background-color: var(--primary-color);
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            box-shadow: 0 2px 5px var(--shadow-color);

            svg {
                height: 1.5em;
                width: 1.5em;
            }
        }
    }

    .btn span {
        display: inline-block;
        margin-left: .5em;
    }
}

.d-block h2 {
    font-size: 1.75em;
    color: var(--text-color)
}

@import "partials/footer.scss";



.main {
    padding-block: 3vw;
}

.container {
    padding-inline: 25vw;
}

@import "partials/animations.scss";

@media screen and (max-width: 1280px) {
    .container {
        padding-inline: 18vw;
    }
}

@media screen and (max-width: 1050px) {
    .container {
        padding-inline: 12vw;
    }
}

@media screen and (max-width: 768px) {
    .navbar {
        .nav-links {
            display: none;
        }

        .sidebar-btn {
            display: block;
        }
    }

    .section {
        .col {
            width: 100%;
            padding: 0 !important;
            text-align: center;
            margin-bottom: 2em;
        }
    }

    .skill-item {
        display: inline-block;
        margin: .75em;
    }

    .section.improve-skills .btn {
        display: block;
        margin-left: 50%;
        transform: translateX(-50%);
    }

    .chief-card {
        box-shadow: 0 5px 10px var(--shadow-color);
    }

    .container {
        padding-inline: 6vw;
    }

    .footer {
        grid-template-columns: repeat(4, 1fr);

        .footer-section:nth-child(1) {
            grid-column: span 4;
        }

        .footer-section:nth-child(2),
        .footer-section:nth-child(3) {
            grid-column: span 2;
        }
    }
}

@media screen and (max-width: 480px) {
    .container {
        padding-inline: 3vw;
    }
}



.DetailWrapper{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

}

.DetailWrapper .image-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 3rem;
    text-align: center;
    border-radius: 2rem;
        color: var(--text-color);
}

.image-container .img-container{
        margin-top: 2rem;
        height: 25rem;
        width: 30rem;
}

.DetailWrapper .info{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 5rem;
    color: var(--text-color);

}

.info .btn-container{

    display: flex;
    flex-direction: row;
    margin-bottom: 2rem;
    margin-left: 1rem;

  

    .button {
            padding: 1rem 2rem;
            color: #313131;
            background: white;
            border: 2px solid black;
            font-weight: 600;
            margin-left: 2rem;
            cursor: pointer;
        }

                .active {
                    background: var(--primary-color);
                    color: white;
                }

}

  



.img-container img{
    
           
            border-radius: 2rem;
            object-fit: contain;
            height: 100%;
            width: 100%;
    
    
    
}

.info h4{
    margin-bottom: 2rem;
}

@media screen and (max-width: 1300px) {
    .DetailWrapper {
    
            flex-direction: column;
            justify-content: center;
            align-items: center;
         
    
        }

    .info .btn-container {
        
                    display: flex;
                    flex-direction: row;
                    margin-bottom: 2rem;
                    margin-top: 2rem;
                    margin-left: 0rem;

    .button {
          
            margin-left: 0.5rem;
        
        }
    }


        .DetailWrapper .info {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-left: 0rem;
    
        }
}

li{
    text-transform: capitalize;
    margin-left: 1rem;
}

@media screen and ( max-width: 500px)
{
    .image-container .img-container {
            height: 20rem;
            width: auto;
        }
}